.App {
  text-align: center;
}

header {
    padding: 50px;
    background: #FAFAFA;
    border: 1px solid #EEE;
    border-radius: 0 0 50px 50px;
}

header h3 {
  padding-top: 10px;
  padding-right: 35px;
  color: purple;
}

div.card { border-radius: 10px !important; }

h1, h4, ul { float: left; width: 100%; } 

.back-to-home {
  padding: 15px 40px;
  border-radius: 20px;
  float: left;
  margin-top: 20px;
}

h1 { 
  color: #FFF;
  text-shadow: 2px 2px #333;
} 

h4 {
  margin-top: 20px;
  font-weight: 500;
}

.launch-page h4 {
  font-weight: 300;
}

div.card .btn-secondary {
  font-size: 24px;
  background: #555;
  margin-top: 12px;
}

div.my-3 p {
  color: #222; 
  font-size: 21px;
}

div.my-3 {
  background: #F8F8F8;
  width: 100%;
  padding: 20px;
  border: 7px solid rgba(110,50,120,0.1);
  border-radius: 10px;
  float: left;
}

div.launch-page {
  padding-bottom: 50px;
  float: left;
}

div.launch-page h4 {
  color: #FFF;
}

#root {
  background: url("spacex_bg.jpg");
  background-position: center;
  background-attachment: fixed;
  background-size: cover;
  box-shadow: 2px 2px 250px rgb(110,90,130) inset;
  float: left;
  width: 100%;
}

.faded-bg {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  min-height: 100%;
  height: 100%;
  background: rgba(110,50,120,0.7);
  max-height: 100%;
}

.container {
  position: relative;
  z-index: 3;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
